<template>
  <div>
    <el-dialog title="收货地址" :visible="dialogFormVisible"  width="30%" :show-close="false">
      <el-form :model="form" label-position="left" ref="updateForm">
        <el-form-item label="管理员名称" :label-width="formLabelWidth">
          <el-input v-model="form.user_name" autocomplete="off" ></el-input>
        </el-form-item>
        <el-form-item label="管理员电话" :label-width="formLabelWidth">
          <el-input v-model="form.user_tel" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="管理权限" :label-width="formLabelWidth">
          <el-switch active-color="#13ce66" inactive-color="#ff4949" v-model=form.user_auth>
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="updateUser">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      width="30%"
      >
      <span>确定删除吗？</span>
      <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="deleteUser">确 定</el-button>
     </span>
    </el-dialog>
    <el-col>
      <el-row style="margin-bottom: 20px">
        <el-col :span="20">
          <div class="title-expand">
            <h2>{{company_name}}</h2>
          </div>
        </el-col>
        <el-col :span="1">
          <el-button type="primary" icon="el-icon-circle-plus" @click="addData">添加管理员</el-button>
        </el-col>

      </el-row>
      <el-table
        :data="tableData.filter(data => !search || data.user_tel.toLowerCase().includes(search.toLowerCase()))"
        style="width: 100%">
        <el-table-column
          label="添加日期"
          prop="add_time">
        </el-table-column>
        <el-table-column
          label="管理者名字"
          prop="user_name">
        </el-table-column>
        <el-table-column
          label="管理者电话"
          prop="user_tel">
        </el-table-column>
        <el-table-column
          label="是否可以修改设备"
          prop="user_auth"
        >
          <template slot-scope="scope">
            <el-switch active-color="#13ce66" inactive-color="#ff4949" v-model="scope.row.user_auth" :disabled="true">
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column
          align="right">
          <template slot="header" slot-scope="scope">
            <el-input
              v-model="search"
              size="mini"
              placeholder="输入电话搜索"/>
          </template>

          <template slot-scope="scope">
            <el-button
              size="mini"
              type="warning"
              @click="handleEdit(scope.$index, scope.row)">修改</el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table></el-col>
  </div>

</template>

<script>
  export default {
    data() {
      return {
        company_name:'',
        company_id:'',
        tableData: [],
        search: '',
        dialogFormVisible: false,
        formLabelWidth: '120px',
        dialogVisible:false,
        tobeDelete:'',
        form: {
          user_name: '',
          user_auth: true,
          user_tel:''
        },
        org_form:{
          user_name: '',
          user_auth: true,
          user_tel:''
        }
      }
    },
    methods: {
      initData(){
        let th = this
        this.axios.get('/api/get_all_user',{
          params:{
            company_id:this.company_id,
          }
        }).then(function(res){
          console.log(res.data.data)
          th.tableData=res.data.data
        }).catch(function (error) {
          console.log('come')
          console.log(error);
        });
      },
      handleEdit(index, row) {
        console.log(index, row);
        this.dialogFormVisible=true
        this.form=row
      },
      handleDelete(index, row) {
        this.dialogVisible=true
        this.tobeDelete=row.user_tel
      },
      change(index, row){
        console.log(index, row.status);
      },
      addData(){
        this.dialogFormVisible=true
        this.form = this.org_form
      },
      updateUser(){
        this.dialogFormVisible=false
        let th = this
        this.$refs.updateForm.validate(valid => {
          if (valid) {
            if (typeof (this.form.id)==="undefined"){
              console.log('add')
              this.axios.get('/api/add_user',{
                params:{
                  user_name:this.form.user_name,
                  user_auth:this.form.user_auth,
                  user_tel:this.form.user_tel
                }
              }).then(function(res){
                if (res.data.status===200){
                  th.initData()
                }
                console.log(res.data)
              }).catch(function (error) {
                console.log(error);
              });
            }else {
              console.log('update')
              this.axios.get('/api/update_user',{
                params:{
                  user_name:this.form.user_name,
                  user_auth:this.form.user_auth,
                  user_tel:this.form.user_tel,
                  user_id:this.form.id
                }
              }).then(function(res){
                if (res.data.status===200){
                  th.initData()
                }
              }).catch(function (error) {
                console.log(error);
              });
            }
          }
        })
      },
      deleteUser(){
        console.log('ss')
        this.dialogVisible=false
        let th = this
        this.axios.get('/api/delete_user',{
          params:{
            user_tel:this.tobeDelete,
          }
        }).then(function(res){
          if (res.data.status===200){
            th.initData()
          }
        }).catch(function (error) {
          console.log(error);
        });
      }
    },
    created() {
      console.log(this.$route.params)
      this.company_name = this.$route.params['company_name']
      this.company_id = this.$route.params['company_id']
      this.initData()
    }


  }
</script>



