import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Manager from '@/components/Manager'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/manager',
      name: 'Manager',
      component: Manager
    }
  ]
})



